export function initFaqTabs () {
  const tabs = document.querySelectorAll("[data-target-destination]");
  const faqs = document.querySelectorAll("[data-faq-destination]");

  function showFAQsFor(destinationId) {
    faqs.forEach(faq => {
      const matchesDestination = faq.getAttribute("data-faq-destination") === destinationId || faq.getAttribute("data-faq-destination") === "";
      faq.style.display = matchesDestination ? "block" : "none";
    });
  }

  tabs.forEach(tab => {
    tab.addEventListener("click", () => {
      const selectedId = tab.getAttribute("data-target-destination");

      // UI update for active tab (on anchor child)
      tabs.forEach(t => {
        const anchor = t.querySelector("a");
        if (anchor) anchor.classList.remove("active");
      });

      const currentAnchor = tab.querySelector("a");
      if (currentAnchor) currentAnchor.classList.add("active");

      showFAQsFor(selectedId);

      // Show/hide "no FAQs" message if all children are hidden
      document.querySelectorAll('.faq-list-wrapper').forEach(wrapper => {
        const children = Array.from(wrapper.children).filter(child => child.hasAttribute('data-faq-destination'));
        const allHidden = children.length > 0 && children.every(child => child.style.display === 'none');
        const section = wrapper.closest('.section');
        if (section) {
          section.style.display = allHidden ? 'none' : '';
        }
      });
    });
  });

  //Trigger first tab
  if (tabs.length > 0) {
    tabs[0].click();
  }
}